.movieList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.movieItem img {
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14),
        0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.movieItem img:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}

.pageTitle {
    color: #fff;
    font-size: 48px;
    margin-top: 0;
}

.movieTitle {
    color: #fff;
    margin-top: 8px;
    margin-bottom: 48px;
    font-size: 14px;
}
